<template>
	<div id="kefu-chat">
		<public-header :title="fans.nickname"></public-header>
		<div class="content font-14">
			<div class="connection" v-if="fans.mobile">
				<div class="status" v-if="fans.isonline == 1">{{fans.online_cn}}</div>
				<div class="call flex c-info">
					<div class="flex" @click="util.jsTel(fans.mobile)">
						<i class="icon icon-telephone"></i>
						<span>{{fans.mobile_cn}}</span>
					</div>
				</div>
			</div>
			<div class="chatlog" :style="{bottom: chatLogBottom + 'px'}" ref="chatlog">
				<van-pull-refresh v-model="chatlog.loading" @refresh="onLoadMore()">
					<template v-if="chatlog.data.length > 0">
						<template v-for="(chatlogItem, index) in chatlog.data">
							<div class="log-item" :class="{left: chatlogItem.isleft == 1, right: chatlogItem.isleft == 0}">
								<div class="time flex-center margin-15-b" v-if="chatlogItem.addtime_cn">
									<span>{{chatlogItem.addtime_cn}}</span>
								</div>
								<div class="detail">
									<div class="avatar">
										<img class="img-100" :src="chatlogItem.avatar" alt="">
									</div>
									<div class="text before" v-if="chatlogItem.type == 'text'">{{chatlogItem.content}}</div>
									<div class="image" @click="util.jsPreviewImage(chatlogItem.content)" v-else-if="chatlogItem.type == 'image'">
										<img :src="chatlogItem.content" alt="">
									</div>
									<div class="order before" v-else-if="chatlogItem.type == 'orderTakeout'" @click="util.jsUrl('pages/order/detail', {id: chatlogItem.orderid});">
										<div class="c-gray">订单信息</div>
										<div class="flex-lr margin-10-t">
											<div class="store-logo">
												<img class="img-100" :src="chatlogItem.content.logo" alt="">
											</div>
											<div class="order-info">
												<div class="flex-lr">
													<div class="store-title ellipsis">{{chatlogItem.content.title}}</div>
													<div class="order-status font-12 ellipsis">{{chatlogItem.content.status_cn}}</div>
												</div>
												<div class="flex-lr c-gray">
													<div class="goods-title ellipsis">{{chatlogItem.content.goods_title}}</div>
													<div class="order-fee font-12 ellipsis">实付<span class="c-default">{{Lang.dollarSign}}{{chatlogItem.content.final_fee}}{{Lang.dollarSignCn}}</span></div>
												</div>
											</div>
										</div>
									</div>
									<div class="status" :class="{'not-read': chatlogItem.isread == 0}" v-if="0">{{chatlogItem.isread == 0 ? '未读' : '已读'}}</div>
								</div>
							</div>
						</template>
					</template>
					<div class="log-item center" v-if="0">
						<div class="tips">商家可能正在忙，未能及时回复，请直接的电话联系</div>
					</div>
				</van-pull-refresh>
			</div>
			<div class="tools" v-if="chat.status == 1">
				<div class="guess flex padding-15-lr padding-10-tb van-hairline--bottom">
					<span class="c-gray margin-10-r">猜你喜欢</span>
					<ul class="guess-list">
						<li class="guess-item" @click="onShowOrders()">最近订单</li>
					</ul>
				</div>
				<div class="main flex-lr">
					<i class="icon icon-voicefill margin-15-r" @click="onSendMessage()" v-if="0"></i>
					<input v-model="content" type="text" class="text" placeholder="输入消息..." @keyup.enter="onSendMessage()" @blur="onBlur" @focus="onFocus">
					<i class="icon icon-sort margin-15-lr" @click="onToggleStatus('fastReply')"></i>
					<i class="icon icon-add" @click="onToggleStatus('others')" v-if="!content"></i>
					<div class="btn-send" @click="onSendMessage()" v-else>发送</div>
				</div>
				<ul class="common-reply van-hairline--top" v-if="status.fastReply">
					<template v-for="(item, index) in fastReply">
						<li class="reply-item van-hairline--bottom" @click="onSendMessage(item, 'text')">{{item}}</li>
					</template>
					<li class="reply-item flex-center c-info">
						<i class="icon icon-add font-18 margin-5-r"></i>
						<span @click="onTogglePopup('fastReply')">添加常用语</span>
					</li>
				</ul>
				<ul class="others" v-if="status.others">
					<li class="other-item">
						<div class="other-item-inner">
							<input class="weui-uploader__input" type="file" multiple="multiple" accept="image/*" @change="onUploadImage($event)">
							<i class="icon icon-pic-filling"></i>
							<span class="margin-10-t">照片</span>
						</div>
					</li>
				</ul>
			</div>
			<div class="close-tips ellipsis van-hairline--top" v-else>{{chat.reason}}</div>
		</div>
		<van-popup v-model="popup.fastReply" class="fast-reply-popup">
			<div class="title">添加快捷短语</div>
			<van-field
				v-model="faseReplyMsg"
				type="textarea"
				placeholder="例如: 我不能吃辣, 麻烦少放些辣椒"
				rows="4"
				class="border-0px"
			/>
			<div class="flex-center van-hairline--top">
				<div class="flex-1 flex-center padding-15-tb van-hairline--right" @click="onTogglePopup('fastReply')">取消</div>
				<div class="flex-1 flex-center padding-15-tb c-gray" @click="onConfirmFastReply">确认添加</div>
			</div>
		</van-popup>
		<van-popup v-model="popup.order" position="bottom" class="order-list-popup">
			<div class="title padding-15 flex-lr van-hairline--bottom">
				<div class="font-15">点击发送订单<span class="c-gray font-12 margin-10-l">展示最近5个订单</span></div>
				<i class="icon icon-close font-20" @click="onTogglePopup('order')"></i>
			</div>
			<div class="popup-content">
				<div
					class="order-item padding-15-l bg-default font-15"
					v-for="(item, index) in orders"
					:key="item.id"
					@click="onOrderClick(item.id)"
				>
					<div class="item-inner " :class="{'van-hairline--top': index > 0}">
						<div class="store-logo">
							<img class="img-100" :src="item.logo" alt="">
						</div>
						<div class="info">
							<div class="flex-lr w-100">
								<div class="store-title ellipsis">{{item.title}}</div>
								<div class="order-status ellipsis">{{item.status_cn}}</div>
							</div>
							<div class="flex-lr c-gray padding-10-t">
								<div class="goods-title ellipsis">{{item.goods_title}}</div>
								<div class="order-fee ellipsis">
									实付<span class="c-default">{{Lang.dollarSign}}{{item.final_fee}}</span>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="flex-center padding-15 van-hairline--top c-danger bg-default font-15" v-if="0">查看全部订单</div>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import {getData, setData} from '@/controller/funcCommon'
	import { wssUrl } from "@/config/env"
	export default {
		data() {
			return {
				relation: 'member2clerk',
				fansopenid: 0,
				orderid: 0,

				chat: {
					status: 1
				},
				kefu: {},
				fans: {},
				order: {
					id: 0,
				},
				chatlog: {
					min: 0,
					psize: 100,
					loading: false,
					finished: false,
					data: []
				},
				content: '',
				fastReply: [],
				faseReplyMsg: '',
				orders: [],
				websocket: {},
				heartCheck: {},
				status: {
					fastReply: false,
					others: false,
					voice: false,
				},
				popup: {
					fastReply: false,
					order: false
				},
				showPreLoading: true,
				islegal: false
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/kefu/clerk/chat',
					data: {
						relation: that.relation,
						fansopenid: that.fansopenid,
						kefuunionid: that.kefuunionid,
						orderid: that.orderid,
						min: that.chatlog.min,
						psize: that.chatlog.psize
					},
					success: (result) => {
						that.chat = that.util.extend(that.chat, result.chat);
						that.kefu = that.util.extend(that.kefu, result.kefu);
						that.fans = that.util.extend(that.fans, result.fans);
						that.order = that.util.extend(that.order, result.order);
						that.fastReply = [...result.reply];
						if(result.chatlog.logs && result.chatlog.logs.length > 0) {
							that.chatlog.data = [...result.chatlog.logs];
							if(result.chatlog.logs.length < that.chatlog.psize) {
								that.chatlog.finished = true;
							}
						}
						that.chatlog.min = result.chatlog.min;
						if(!that.chatlog.min) {
							that.chatlog.finished = true;
						}
						that.util.setWXTitle(that.fans.nickname);
					}
				})
			},

			onLoadMore() {
				var that = this;
				if(that.chatlog.finished) {
					that.$nextTick(() => {
						that.chatlog.loading = false;
					})
					that.util.$toast('没有更多消息了');
					return false;
				}
				getData({
					vue: that,
					url: 'manage/kefu/clerk/more',
					data: {
						chatid: that.chat.id,
						min: that.chatlog.min,
						psize: that.chatlog.psize
					},
					success: (result) => {
						if(result.chatlog.logs && result.chatlog.logs.length > 0) {
							that.chatlog.data = result.chatlog.logs.concat(that.chatlog.data);
							if(result.chatlog.logs.length < that.chatlog.psize) {
								that.chatlog.finished = true;
							}
						}
						that.chatlog.min = result.chatlog.min;
						if(!that.chatlog.min) {
							that.chatlog.finished = true;
						}
						that.chatlog.loading = false;
					}
				})
			},

			onSendMessage(content, type) {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				if(!content) {
					content = that.content;
				}
				if(!type) {
					type = 'text';
				}
				if(!content || content == '') {
					return false;
				}
				setData({
					vue: that,
					url: 'manage/kefu/clerk/addchat',
					data: {
						chatid: that.chat.id,
						type: type,
						content: content
					},
					success: (result) => {
						that.chatlog.data.push(result.log);
						if(type == 'text') {
							that.content = '';
						} else if(type == 'orderTakeout') {
							that.hasSendOrder = true;
						}
					}
				})
			},

			onConfirmFastReply() {
				var that = this;
				if(!that.faseReplyMsg || that.faseReplyMsg == '') {
					return false;
				}
				setData({
					vue: that,
					url: 'manage/kefu/clerk/addreply',
					data: {
						content: that.faseReplyMsg,
						relation: that.relation,
					},
					success: (result) => {
						if(result.reply && result.reply.length > 0) {
							that.fastReply = [...result.reply];
						}
						that.faseReplyMsg = '';
						that.onTogglePopup('fastReply');
					}
				})
			},

			onUploadImage(e) {
				var that = this;
				that.$toast.loading({
					mask: true,
					message: '上传中...',
					duration: 0,
				});
				that.util.image({
					obj: e,
					success: function(obj, result) {
						if(result.url && result.filename) {
							var content = result.filename;
							that.islegal = true;
							that.onSendMessage(content, 'image');
							that.$toast.clear();
						}
					},
					options: {channel: 'h5'}
				});
			},

			onScrollBottom() {
				var that = this;
				that.islegal = true;
				setTimeout(() => {
					var chatLogDom = that.$refs.chatlog;
					chatLogDom.scrollTop = chatLogDom.scrollHeight;
				}, 300);
			},

			onToggleStatus(key) {
				var that = this;
				for(var x in that.status) {
					if(x == key) {
						that.status[x] = !that.status[x];
					} else {
						that.status[x] = false;
					}
				}
			},

			onTogglePopup(key) {
				if(!key) {
					return false;
				}
				var that = this;
				that.popup[key] = !that.popup[key];
			},

			onBlur() {
				var that = this;
				//处理input框失焦时，页面下部留白的问题
				window.scroll(0, 0);
			},

			onFocus() {
				var that = this;
				that.status.fastReply = false;
				that.status.others = false;
				//处理input框聚焦时，键盘遮挡页面的问题
				document.body.scrollTop = document.body.scrollHeight
			},

			onSetNotreadZero() {
				var that = this;
				setData({
					vue: that,
					url: 'manage/kefu/clerk/zero',
					data: {
						chatid: that.chat.id
					},
					success: (result) => {
						console.log('清零成功');
					}
				})
			},

			onOrderClick(orderId) {
				var that = this;
				if(orderId > 0) {
					that.onSendMessage(orderId, 'orderTakeout');
					that.onTogglePopup('order');
				}
			},

			onShowOrders() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/kefu/clerk/order',
					data: {
						chatid: that.chat.id,
					},
					success: (result) => {
						var orders = result.orders;
						if(orders && orders.length > 0) {
							that.orders = [...orders];
							that.onTogglePopup('order');
						} else {
							that.util.$toast('该顾客最近未在您的门店下过单')
						}
					}
				})
			},

			onGetMessage(result){
				var that = this;
				if(that.chat && that.chat.id == result.chat.chatid) {
					setTimeout(() => {
						that.chatlog.data.push(result.chat);
						that.onSetNotreadZero();
					}, 200)
				}
			},

			initWebSocket(){
				var that = this;
				that.iwebsocket.onGetMessage = that.onGetMessage;
			},
		},
		mounted() {
			var that = this;
			var query = that.util.parseQuery(that.$route.query);
			that.relation =  query.relation;
			that.fansopenid = query.fansopenid;
			that.kefuunionid = query.kefuunionid;
			that.orderid = query.orderid;
			that.onLoad();
			that.initWebSocket();
		},
		destroyed() {
			var that = this;
			that.iwebsocket.onGetMessage = null;
		},
		updated(){
			var that = this;
			that.onScrollBottom();
		},
		components: {
			PublicHeader,
		},
		computed: {
			chatLogBottom: function() {
				var that = this;
				var bottom = 96;
				if(that.chat.status == 2) {
					bottom = 56;
				} else {
					if(that.status.fastReply) {
						bottom = 272;
					} else if(that.status.others) {
						bottom = 237;
					}
				}
				return bottom;
			}
		}
	}
</script>

<style>
	@import "../../../static/css/test.css";
	#kefu-chat .connection{
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 44px;
		background-color: #fff;
		padding: 10px 15px;
		font-size: 14px;
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#kefu-chat .connection .status{
		position: absolute;
		top: 10px;
		left: 15px;
		padding: 5px 5px 5px 15px;
		background-color: rgba(32,203,135,0.1);
		color: #20cb87;
	}
	#kefu-chat .connection .status:before{
		content: '';
		position: absolute;
		top: 9px;
		left: 5px;
		width: 6px;
		height: 6px;
		border-radius: 50%;
		background-color: #20cb87;
	}
	#kefu-chat .tools{
		position: absolute;
		z-index: 11;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
	}
	#kefu-chat .tools .guess .guess-list{
		flex: 1;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-start;
	}
	#kefu-chat .tools .guess .guess-list .guess-item{
		border: 1px solid #f0f0f0;
		padding: 4px 10px;
		border-radius: 50px;
		font-size: 13px;
		color: #333;
		margin-right: 10px;
	}
	#kefu-chat .tools .main{
		padding: 10px 15px;
	}
	#kefu-chat .tools .main .icon{
		font-size: 18px;
	}
	#kefu-chat .tools .main .text{
		flex: 1;
		height: 33px;
		padding: 0 15px;
		border-radius: 50px;
		background: #f5f5f5;
	}
	#kefu-chat .tools .common-reply{
		height: 176px;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
	}
	#kefu-chat .tools .common-reply::-webkit-scrollbar {
		display: none;
	}
	#kefu-chat .tools .common-reply .reply-item{
		padding: 15px;
		text-align: center;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	#kefu-chat .tools .others{
		height: 141px;
		display: flex;
		flex-flow: row wrap;
		background-color: #f5f5f5;
		padding-bottom: 15px;
	}
	#kefu-chat .tools .others .other-item{
		width: 25%;
		height: 63px;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		padding-top: 15px;
	}
	#kefu-chat .tools .others .other-item .other-item-inner{
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		position: relative;
	}
	#kefu-chat .tools .others .other-item .other-item-inner .weui-uploader__input{
		display: block;
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		opacity: 0;
	}
	#kefu-chat .tools .others .other-item .icon{
		font-size: 24px;
	}
	#kefu-chat .chatlog{
		position: absolute;
		top: 44px;
		left: 0;
		right: 0;
		bottom: 53px;
		padding: 0 15px 15px 15px;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
	}
	#kefu-chat .chatlog::-webkit-scrollbar {
		display: none;
	}
	#kefu-chat .chatlog .log-item{
		margin-top: 15px;
	}
	#kefu-chat .chatlog .log-item .time span{
		color: #f8f8f8;
		background-color: #cecece;
		font-size: 12px;
		padding: 4px 5px;
		border-radius: 5px;
	}
	#kefu-chat .chatlog .log-item .detail{
		display: flex;
		align-items: flex-start;
	}
	#kefu-chat .chatlog .log-item.left .detail{
		flex-flow: row nowrap;
		padding-right: 40px;
	}
	#kefu-chat .chatlog .log-item.right .detail{
		flex-flow: row-reverse nowrap;
		padding-left: 40px;
	}
	#kefu-chat .chatlog .log-item .detail .text{
		position: relative;
		background-color: #fff;
		padding: 10px;
		border-radius: 5px;
		line-height: 1.3;
		margin: 0 10px 0 10px;
	}
	#kefu-chat .chatlog .log-item.right .detail .text{
		background-color: #33aafc;
		color: #fff;
	}
	#kefu-chat .chatlog .log-item .detail .avatar{
		min-width: 40px;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		overflow: hidden;
	}
	#kefu-chat .chatlog .log-item .detail .status{
		align-self: flex-end;
		white-space: nowrap;
		font-size: 12px;
		color: #999;
		padding-bottom: 3px;
	}
	#kefu-chat .chatlog .log-item .detail .status.not-read{
		color: #33aafc;
	}
	#kefu-chat .chatlog .log-item .detail .before:before{
		content: "";
		display: block;
		border-width: 8px;
		position: absolute;
		top: 12px;
		border-style: solid dashed dashed;
		font-size: 0;
		line-height: 0;
		z-index: 10;
	}
	#kefu-chat .chatlog .log-item.left .detail .before:before{
		left: -16px;
		transform: rotate(90deg);
		border-color: #fff transparent transparent;
	}
	#kefu-chat .chatlog .log-item.right .detail .before:before{
		right: -16px;
		transform: rotate(-90deg);
		border-color: #33aafc transparent transparent;
	}
	#kefu-chat .chatlog .log-item .detail .order{
		position: relative;
		padding: 10px;
		flex: 1;
		margin: 0 10px;
		border-radius: 5px;
		background-color: #fff;
		border: 3px solid #33aafc;
	}
	#kefu-chat .chatlog .log-item.left .detail .order{
		border-color: #fff;
	}
	#kefu-chat .chatlog .log-item .detail .order .store-logo{
		width: 40px;
		height: 40px;
		border-radius: 50%;
		overflow: hidden;
		min-width: 40px;
	}
	#kefu-chat .chatlog .log-item .detail .order .order-info{
